.wrapper {
    display: flex;
    align-items: center;
    padding: 9px 16px;
    color: black;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
        cursor: pointer;
    }
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.info {
    flex: 1;
    margin-left: 12px;
    display: flex;
    flex-direction: column;
}

.name {
    font-size: 1.6rem;
    font-weight: 500;
}

.check {
    margin-left: 8px;
    color: rgb(32, 213, 236);
    font-size: 1.2rem;
}

.username {
    font-size: 1.4rem;
    color: rgba(22, 24, 35, 0.6);
}
